import React, { useEffect, useState } from 'react';
import '../scss/Home.scss'

let sentences = [
    '21세기 조선에서 자유를 꿈꾸다',
    '백성을 살피어 계좌를 인도하시니',
    '그 끝이 하늘에 닿아 자유에 이르게 하셨다'
    ];



function Home() {
    return (
        <section className="home">
            <div className="inner">
                <div className="home__banner" style={{whiteSpace: 'nowrap'}}>
                    {
                        [...sentences].map( (sen, i) => {
                            return <TypeWriter sen={sen} i={i} key={i}></TypeWriter>
                        })
                    }
                </div>
                <p>충&nbsp;이&nbsp;실&nbsp;록</p>
            </div>
        </section>
    )
}

function TypeWriter(props) {
    let [sentence,setSentence] = useState('')
    let text = '';

    useEffect(()=>{
        [...props.sen].forEach((char,i)=>{
            setTimeout(()=>{setSentence(text+=char)},
            100*i+2000*props.i)
        })
    },[])

    return <p>{sentence}</p>
    
}


export default Home;